@use "mixins" as *;

.m31 {
	position: relative;
	background-color: var(--color-text-alt);
	display: grid;

	&__content {
		grid-row: 1;
		grid-column: 1;
		min-width: 0;
	}

	&.is-initialized.is-visible &__layer {
		transition: background-position-y ease .5s;
	}

	&__layer {
		grid-row: 1;
		min-width: 0;
		grid-column: 1;
		background-size: cover;
		pointer-events: none;
		background-position-x: center;
		background-position-y: calc(50% + ((var(--scrolled) - var(--offset)) / var(--scroll-ratio)) * -1px);
		@include themeHide;

		&--1 {
			background-image: url(/src/assets/static/img/space/1.webp);
			--scroll-ratio: 3;
		}

		&--2 {
			background-image: url(/src/assets/static/img/space/2.webp);
			--scroll-ratio: 4;
		}

		&--3 {
			background-image: url(/src/assets/static/img/space/3.webp);
			--scroll-ratio: 3;
		}

		&--4 {
			background-image: url(/src/assets/static/img/space/4.webp);
			--scroll-ratio: 2;
		}
	}
}