@use "mixins" as *;
@use "container" as *;

.generic {
	display: flex;
	flex-direction: column;
	gap: gap-scale(1);
	@extend %container-fluid;

	&__header {
		display: flex;
		flex-direction: column;
		gap: 8px;

		:is(h1, h2, h3, h4, h5, h6) {
			color: var(--color-primary);
		}

		a {
			@include hoverType1;
		}
	}

	&__content {
		display: flex;
		flex-direction: column;
		gap: inherit;
	}
}