@use "icon" as *;
@use "font" as *;
@use "mixins" as *;
@use "custom-list" as *;
@use "breakpoint" as *;

.table,
%table {
	width: 100%;
	border-collapse: collapse;

	&--slim {
		max-width: 500px;
	}

	&--small {
		@extend %font-2;
		--icon-size: 16;
	}

	&--bordered tr {
		border-bottom: 1px solid var(--default-border-color);
	}

	&-wrapper {
		padding-bottom: 8px;
		position: relative;
		overflow: auto;
		display: flex;

		@media (max-width: $tablet) {
			@include removeContainerPaddig;
			@include addContainerPaddig;
		}

		// background:
		// 	linear-gradient(90deg, white 50%,
		// 		rgba(255, 255, 255, 0)) center left,
		// 	linear-gradient(90deg, rgba(255, 255, 255, 0),
		// 		white 50%) center right,
		// 	radial-gradient(farthest-side at 0 50%,
		// 		rgba(0, 0, 0, 0.2),
		// 		rgba(0, 0, 0, 0)) center left,
		// 	radial-gradient(farthest-side at 100% 50%,
		// 		rgba(0, 0, 0, 0.2),
		// 		rgba(0, 0, 0, 0)) center right;
		// background-repeat: no-repeat;
		// background-size: 40px 100%, 40px 100%, 14px 100%, 14px 100%;
		// background-attachment: local, local, scroll, scroll;
	}

	ul,
	ol {
		@extend %custom-list;
	}

	thead {
		background-color: var(--color-primary-lighter);

		// mix-blend-mode: darken;
		a {
			display: flex;
			align-items: center;
			justify-content: center;
			gap: 4px;
			transition: gap ease var(--default-duration);

			&[data-sort-order] {
				&::after {
					content: "";
					@extend %icon, %icon--sort;
					transition: opacity ease var(--default-duration);
				}
			}

			&[data-sort-order="asc"] {
				&::after {
					transform: scaleY(-1);
				}
			}
		}
	}

	&,
	th,
	tbody {
		border: var(--default-border);
	}

	tr:hover {
		background-color: var(--color-primary-lighter);
		// mix-blend-mode: darken;
	}

	&:has([colspan], [rowspan]) {
		@extend %table--bordered;
		// border-bottom: var(--default-border);
		// border-top: var(--default-border);
	}

	td,
	th {
		padding: 15px 9px;

		a,
		button:not([class]) {
			@include hoverType1;
		}
	}

	td {
		vertical-align: top;
	}

	th {
		color: var(--color-primary);

		&:has(input) {
			width: 0;
			min-width: min-content;
		}
	}
}

.table-mega,
%table-mega {
	border-collapse: collapse;
	max-height: 100%;
	display: grid;
	overflow: auto;
	outline: var(--default-border);
	outline-offset: calc(var(--default-border-width) * -1);

	thead tr:first-child th {
		grid-row-start: 1;
	}

	tbody,
	thead,
	tr {
		display: contents;
	}

	[colspan="2"] {
		grid-column-end: span 2;
	}

	[rowspan="2"] {
		grid-row-end: span 2;
	}

	th {
		border: var(--default-border);
		margin-top: -2px;
		margin-left: -2px;
		background-color: var(--color-primary-lighter);
	}

	thead,
	tbody tr:hover {
		background-color: var(--color-primary-lighter);
	}

	tr:has(td:hover) td {
		background-color: var(--color-primary-lighter);
	}

	td,
	th {
		padding: 6px;
	}

	th {
		color: var(--color-primary);
	}
}