@use "mixins" as *;
@use "font" as *;
@use "button" as *;
@use "icon" as *;

.select-simple,
%select-simple {
	display: flex;
	flex-direction: column;
	gap: 6px 0;
	@extend %font-2;
	flex-grow: 1;
	--icon-size: 20;
	--pr: 4px;

	&--big select {
		min-height: var(--input-height);
	}

	&__select {
		display: flex;
		position: relative;
		align-items: center;
		color: var(--color-primary);

		&::after {
			content: "";
			@extend %icon, %icon--chevron_down;
			pointer-events: none;
			position: absolute;
			right: var(--pr);
		}
	}

	select {
		min-height: var(--input-height-small);
		padding: 2px calc(var(--pr) + var(--icon-size) * 1px + var(--pr)) 2px 8px;
		@extend %font-2;
		cursor: pointer;
		width: 100%;
		display: flex;
		align-items: center;
		@include hoverTypeBorder;
		background-color: var(--color-text-alt);
		border: var(--default-border);
		border-radius: var(--default-border-radius-small);

		@layer unset {
			& {
				all: unset;
			}
		}
	}
}